import React from 'react';
import {BrowserRouter as Router, Link, Route} from 'react-router-dom'
import Home from './Home';
import Personal from './Personal';
import Blog from './Blog';
import Landing from './Landing';
import logo from '../images/favicon.ico'


export default class TopBar extends React.Component {
    constructor(props) {
        super(props);
        this.logo = logo;
    }

    handleClick() {
        console.log("clicking brand name button")
    }

    render() {
        return(
            <Router>
                <nav className="navbar navbar-brand-expand-lg navbar-expand-lg navbar-light">
                    <Link to="/">
                        <img className="nav-bar brand" src={this.logo} alt="Snow"/>
                    </Link>
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                            <Link className="nav-link" to="/professional">Professional</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/personal">Personal</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link"to="/blog">Blog</Link>
                        </li> 
                    </ul>
                </nav>
                <div className="routes">
                    <Route path="/professional"
                           render={() => <Home/>}/>
                    <Route path="/personal"
                           render={() => <Personal/>}/>
                    <Route path="/blog"
                           render={() => <Blog/>}/>
                    <Route exact
                           path="/"
                           render={() => <Landing/>}/>
                </div>
            </Router>
        )
    }
}
